<template>
  <div class="home">
    <div class="banner pr">
      <div class="pa time">
        <span class="date">{{date}}</span>
        <div class="pa year">
          <span class="f20">{{day | dayFilter}}</span>
          <span>{{month}},{{year}}</span>
        </div>
      </div>
      <el-button type="danger" icon="plus" @click="ShowDia(true)"></el-button>
      <el-dialog title="add New" :visible="addShow" :close-on-click-modal="true" :before-close="close">
        <el-steps :space="100" :active="active" finish-status="success">
          <el-step title="步骤 1"></el-step>
          <el-step title="步骤 2"></el-step>
        </el-steps>
        <el-input v-model="title" placeholder="请输入标题" v-if="active==0"></el-input>
        <el-input
          type="textarea"
          :rows="2"
          placeholder="请输入内容"
          v-model="textarea" v-if="active==1">
        </el-input>
        <el-button style="margin-top: 12px;" @click="next">下一步</el-button>
      </el-dialog>
    </div>
    <list></list>
  </div>
</template>

<script>
  import list from 'components/ShowList'
  import {mapState, mapMutations, mapActions, mapGetters} from 'vuex'
  export default {
    name: 'Home',
    data () {
      return {
        year: '',
        month: '',
        date: '',
        day: '',
        title: '',
        textarea: '',
      }
    },
    components: {
      list
    },
    computed: {
      ...mapState({
        addShow: 'addShow',
        active: 'active',
      })
    },
    watch: {
      active(val){
        if (val == 0) this.title = '', this.textarea = ''
      }
    },
    mounted(){
      this.nowTime();
    },
    filters: {
      dayFilter(val){
        switch (val) {
          case '星期一':
            return 'Monday'
            break;
          case '星期二':
            return 'Tuesday'
            break;
          case '星期三':
            return 'Wednesday'
            break;
          case '星期四':
            return 'Thursday'
            break;
          case '星期五':
            return 'Friday'
            break;
          case '星期六':
            return 'Saturday'
            break;
          default:
            return 'Sunday'
        }
      }
    },
    methods: {
      ...mapActions({
        ShowDia: 'SHOW_DIALOG',//types的映射
        next: 'SET_ACTIVE'
      }),
      next(){
        this.$store.commit('SET_ACTIVE', 1);
        if (this.$store.state.active > 1) {
          this.$store.commit('SET_ACTIVE', 0);
          this.$store.dispatch('SHOW_DIALOG', false);
          let obj = {};
          obj.title = this.title;
          obj.text = this.textarea;
          this.$store.commit('add/ADD_LIST', obj);
        }
      },
      close(){
        this.$store.commit('SHOW_DIALOG', false);//提交的也是store的types映射
      },
      nowTime(){
        let show_day = new Array('星期一', '星期二', '星期三', '星期四', '星期五', '星期六', '星期日');
        let time = new Date();
        let year = time.getFullYear();
        let month = time.getMonth();
        let date = time.getDate();
        let day = time.getDay();
        let hour = time.getHours();
        let minutes = time.getMinutes();
        let second = time.getSeconds();
        month < 10 ? month = `0${month + 1}` : month + 1;
        date < 10 ? date = `0${date}` : date;
        hour < 10 ? hour = '0' + hour : hour;
        minutes < 10 ? minutes = '0' + minutes : minutes;
        second < 10 ? second = '0' + second : second;
        //let now_time = '当前时间：' + year + '年' + month + '月' + date + '日' + ' ' + show_day[day - 1] + ' ' + hour + ':' + minutes + ':' + second;
        this.year = year;
        this.month = month;
        this.date = date;
        this.day = show_day[day - 1];
      }
    }
  }
</script>

<style src="css/style.css" scoped></style>
<style scoped>

</style>



